import { Component, OnInit } from '@angular/core';
import { ApiService } from './apiService';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent {
  constructor( private apiService : ApiService){}

  bankDetails;
  entries=5;
  currentPage=1;
totalRecords=0;
displayBankDetails;
ifsc:String="";
bankId:String=""
branch:String=""
address:String=""
city:String=""
district:String="";
state:String="";
selectedCity:String="MUMBAI";
bankName:String="";

overlay:boolean;

	ngOnInit() {

	this.getBankList()
	}

	getBankList(){
		this.overlay=true;
var url="https://vast-shore-74260.herokuapp.com/banks?city="+this.selectedCity;

		if(localStorage.getItem(url)!=null){
this.bankDetails=JSON.parse(localStorage.getItem(url))
		    		this.displayBankDetails = JSON.parse(localStorage.getItem(url))
this.totalRecords=this.bankDetails.length;
this.overlay=false;
		}
else{

		    this.apiService.getRequest(url).subscribe(
		    	(res) => {
		    		this.bankDetails=res.json()
		    		this.displayBankDetails = res.json()
this.totalRecords=this.bankDetails.length;
localStorage.setItem(url, JSON.stringify(res.json()));
this.overlay=false;},
(err)=>{})
}
	}

	onCityChange(event){
		this.ifsc="";
		this.bankName=""
		this.bankId=""
		this.city=""
		this.district=""
		this.address=""
		this.state=""
		this.branch=""
		this.selectedCity=event;
		this.getBankList();
	}
	nextplus(){
		return this.totalRecords/Number(this.entries) > Number(this.currentPage)
	}	
	nextminus(){
		return Number(this.currentPage) > 1
	}

	nextpage(input){
		if(input=='-')
			this.currentPage--;
		else
			this.currentPage++;
	}

	filter(event){
		this.displayBankDetails=this.bankDetails;
		this.displayBankDetails=this.displayBankDetails.filter(entry=>{
			if(entry.ifsc.toLowerCase().includes(this.ifsc.toLowerCase()) 
				&& entry.bank_id.toString().toLowerCase().includes(this.bankId.toLowerCase()) 
				&& entry.branch.toLowerCase().includes(this.branch.toLowerCase()) 
				&& entry.address.toLowerCase().includes(this.address.toLowerCase())
				&& entry.city.toLowerCase().includes(this.city.toLowerCase())
				&& entry.district.toLowerCase().includes(this.district.toLowerCase())
				&& entry.state.toLowerCase().includes(this.state.toLowerCase())
				&& entry.bank_name.toLowerCase().includes(this.bankName.toLowerCase()))
				return true
		});
		this.totalRecords=this.displayBankDetails.length;
	}

	onClickCheckBox(event,i,data){
		 if (event.target.checked) {
		 	if(localStorage.getItem('fav')!=null){
		 		var fav=JSON.parse(localStorage.getItem('fav'))
		 		fav.push(data);
		 		fav=Array.from(new Set(fav));
                localStorage.setItem("fav", JSON.stringify(fav));
		 	}
		 	else{
		 		var uniqueFav=[];
		 		uniqueFav.push(data);
		 		localStorage.setItem("fav", JSON.stringify(uniqueFav));
		 	}
      		
   		}
   		else{
		 		var fav=JSON.parse(localStorage.getItem('fav'))
		 		fav=fav.filter(entry=>entry!=data);
                localStorage.setItem("fav", JSON.stringify(fav));
   		}
	}

	checkIfFav(data){
		var fav=JSON.parse(localStorage.getItem('fav'))
		if(fav!=null)
		if(fav.indexOf(data)!=-1)return true;
		return false;
	}

}



